import React from 'react'
import '../assets/css/program/2_4_program.scss'
import ProgramList from './component/3_Program_list'


function Program() {
    const program = [
        {title: '', text: ' .\n    .'}, 
        {title: '', text: ''}, 
        {title: '', text: '       \n   !  '}, 
        {title: ' ', text: '  \n  .\n\n  \n  \n  \n \n\n    '}, 
        {title: '   ', text: '  ,\n,     .\n\n  ,\n   ,\n   \n  ,\n   \n\n   .'}
    ]
    const programlist = []
    for(let i=0; i<program.length; i++) {
        programlist.push(<ProgramList program={program[i]}></ProgramList>)
    }
    return ( 
        <>
            <div className="program_title">Program List</div>
            <div className="program_notice">*    1  . </div>
            <div className="program_list_grid">
                {programlist}
            </div>
        </> 
    );
}
    
export default Program;